<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="AppId">
            <el-input v-model.number="requestParam.app_id"></el-input>
          </el-form-item>
          <el-form-item label="渠道码">
            <el-input v-model="requestParam.channel_code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getGameList">查询</el-button>
          </el-form-item>
           <el-form-item v-if="this.$store.getters['user/getUser'].permission==0">
            <el-radio-group v-model="user.permission">
              <el-radio :label="1">管理员</el-radio>
              <el-radio :label="2">客户端</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-button type="success" size="small" @click="addGame"
          >新增游戏</el-button
        >
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="game_key" label="服务器编号" align="center">
          </el-table-column>
          <el-table-column prop="name" label="游戏名称" align="center">
          </el-table-column>
          <el-table-column
            prop="icon"
            label="图标ID 或路径"
            :show-overflow-tooltip="true"
            align="center"
          >
          </el-table-column>
          <el-table-column label="游戏状态(服务器)" align="center">
            <template slot-scope="scope">
              <!-- <span v-text="scope.row"></span> -->
              <div>
                <span v-if="scope.row.admin_status == 0">隐藏</span>
                <span v-else-if="scope.row.admin_status == 1">正常</span>
                <span v-else-if="scope.row.admin_status == 2">维护</span>
                <span v-else>未知状态{{ scope.row.admin_status }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="游戏状态(客户端)"
            align="center"
            v-if="user.permission == 2"
          >
           
            <template slot-scope="scope">
              <div>
                <span v-if="scope.row.status == 0">隐藏</span>
                <span v-else-if="scope.row.status == 1">正常</span>
                <span v-else-if="scope.row.status == 2">维护</span>
                <span v-else>未知状态{{ scope.row.status }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                v-show="user.permission == 1"
                type="primary"
                size="small"
                @click="updateGame(scope.row.game_id)"
                >编辑</el-button
              >
              <el-button
                v-show="user.permission == 1"
                type="danger"
                size="small"
                @click="
                  openChangeStatus(
                    scope.row.game_id,
                    scope.row.admin_status,
                    'admin_status'
                  )
                "
                >切换状态</el-button
              >
                <el-button
                  type="primary"
                  size="small"
                  v-show="user.permission == 2&&scope.row.publish_status==0"
                  @click="pushGame(scope.row.game_id)"
                  >发布</el-button
                >
                 <el-button
                  type="primary"
                  size="small"
                  v-show="user.permission == 2&&scope.row.publish_status==2"
                  @click="pushGame(scope.row.game_id)"
                  >更新</el-button
                >
              <el-button
                v-show="user.permission == 2"
                type="danger"
                size="small"
                @click="
                  openChangeStatus(
                    scope.row.game_id,
                    scope.row.status,
                    'status'
                  )
                "
                >切换客户端状态</el-button
              >
               <el-button
                type="success"
                size="small"
                @click="addRoom(scope.row.game_id)"
                >新增房间</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <template>
          <el-dialog
            :title="action.title"
            :visible.sync="dialogFormVisible"
            show-close:true
          >
            <el-form
              :label-position="labelPosition"
              label-width="100px"
              :model="game"
              class="formStyle text_common"
              size="mini"
              ref="game"
              v-show="action.obj == 'game'"
            >
              <el-form-item label="游戏编号" prop="game_id" :required="true">
                <el-input
                  v-model.number="game.game_id"
                  :disabled="action.type == 'update'"
                  @blur="checkGameId"
                ></el-input>
              </el-form-item>
              <el-form-item label="服务器名称" prop="game_key" :required="true">
                <el-input v-model="game.game_key"></el-input>
              </el-form-item>
              <el-form-item
                label="路由配置"
                prop="lobby_server"
                :required="true"
              >
                key:<el-input
                  v-model="game.lobby_server.key"
                  class="dialog_input_small"
                  placeholder="默认值:lobby"
                ></el-input>
                id:<el-input
                  v-model="game.lobby_server.id"
                  class="dialog_input_small"
                  placeholder="默认值:lobby-server-1"
                ></el-input>
              </el-form-item>
              <el-form-item
                label="货币类型"
                prop="currency_type"
                :required="true"
              >
                <el-input v-model.number="game.currency_type"></el-input>
              </el-form-item>
              <el-form-item label="游戏类型" prop="type">
                <el-input v-model.number="game.type"></el-input>
              </el-form-item>
              <el-form-item label="游戏名称" prop="name">
                <el-input v-model="game.name"></el-input>
              </el-form-item>
              <el-form-item label="游戏图标" prop="icon">
                <el-input v-model.number="game.icon"></el-input>
              </el-form-item>
              <!-- <el-form-item label="游戏状态" prop="status">
                <el-radio-group v-model.number="game.status" size="medium">
                  <el-radio border :label="0">隐藏</el-radio>
                  <el-radio border :label="1">正常</el-radio>
                </el-radio-group>
              </el-form-item> -->
              <el-form-item>
                <el-button type="primary" size="medium" @click="submitGame"
                  >提交</el-button
                >
                <el-button
                  type="danger"
                  size="medium"
                  @click="resetForm('game')"
                  :disabled="action.type == 'update'"
                  >重置</el-button
                >
              </el-form-item>
            </el-form>
            <el-form
              :label-position="labelPosition"
              label-width="200px"
              :model="room"
              class="formStyle text_common"
              size="mini"
              ref="room"
              v-show="action.obj == 'room' && action.type == 'add'"
            >
              <el-form-item label="游戏Id" prop="game_id" :required="true">
                <el-input
                  v-model.number="room.game_id"
                  :disabled="true"
                ></el-input>
              </el-form-item>
              <el-form-item label="房间名" prop="roomName">
                <el-input v-model="room.roomName"></el-input>
              </el-form-item>
              <el-form-item label="游戏图标" prop="roomIcon">
                <el-input v-model.number="room.roomIcon"></el-input>
              </el-form-item>
              <el-form-item label="房间状态" prop="status">
                <el-radio-group v-model.number="room.status" size="medium">
                  <el-radio :label="0">关</el-radio>
                  <el-radio :label="1">开</el-radio>
                  <el-radio :label="2">维护</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="桌子类型" prop="desk_type">
                <el-radio-group v-model.number="room.desk_type" size="medium">
                  <el-radio :label="0">动态</el-radio>
                  <el-radio :label="1">常驻</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="排序" prop="sort">
                <el-input v-model.number="room.sort"></el-input>
              </el-form-item>
              <el-form-item label="可入房间角色" prop="roles">
                <el-input
                  type="textarea"
                  :rows="2"
                  placeholder="用逗号隔开，比如：123,457,457"
                  v-model="arraystr.roles"
                >
                </el-input>
              </el-form-item>
              <el-form-item label="标签" prop="tags">
                <el-input
                  type="textarea"
                  :rows="2"
                  placeholder="用逗号隔开，比如：123,457,457"
                  v-model="arraystr.tags"
                >
                </el-input>
              </el-form-item>
              <el-form-item label="房间类型" prop="type">
                <el-input
                  v-model.number="room.type"
                  placeholder="房间类型,子游戏自定义"
                ></el-input>
              </el-form-item>
              <el-form-item label="锁桌" prop="lock_desk">
                <el-radio-group v-model.number="room.lock_desk" size="medium">
                  <el-radio :label="false">否</el-radio>
                  <el-radio :label="true">是</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item
                label="大厅服务器"
                prop="lobby_server"
                :required="true"
              >
                key:<el-input
                  v-model="room.lobby_server.key"
                  class="dialog_input_small"
                  placeholder="默认值:lobby"
                ></el-input>
                id:<el-input
                  v-model="room.lobby_server.id"
                  class="dialog_input_small"
                  placeholder="默认值:lobby-server-1"
                ></el-input>
              </el-form-item>
              <el-form-item label="开桌货币类型" prop="currency_type">
                <el-radio-group
                  v-model.number="room.game_option.currency_type"
                  size="medium"
                >
                  <el-radio :label="1">筹码</el-radio>
                  <el-radio :label="2">钻石</el-radio>
                  <el-radio :label="3">积分</el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item prop="desk_option">
                <el-form
                  status-icon
                  ref="ruleForm"
                  label-width="100px"
                  class="demo-ruleForm innerForm"
                >
                  <el-form-item label="货币类型" class="innerForm_item">
                    <el-radio-group
                      v-model.number="room.desk_option.enter_currency_type"
                      size="medium"
                    >
                      <el-radio :label="1">筹码</el-radio>
                      <el-radio :label="2">钻石</el-radio>
                      <el-radio :label="3">积分</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item
                    label="抽水率(单位:%)"
                    prop="pump"
                    class="innerForm_item"
                  >
                    <el-input v-model.number="room.desk_option.pump"></el-input>
                  </el-form-item>
                  <el-form-item
                    label="房间底分"
                    prop="roomScore"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.desk_option.roomScore"
                    ></el-input>
                  </el-form-item>
                  <el-form-item
                    label="入场扣费"
                    prop="cost"
                    class="innerForm_item"
                  >
                    <el-input v-model.number="room.desk_option.cost"></el-input>
                  </el-form-item>
                  <el-form-item label="金币准入范围" prop="enter">
                    <el-input
                      v-model.number="room.desk_option.enter[0]"
                      class="minInput"
                    >
                      <template slot="prepend">min</template>
                    </el-input>
                    -
                    <el-input
                      v-model.number="room.desk_option.enter[1]"
                      class="minInput"
                    >
                      <template slot="append">max</template>
                    </el-input>
                  </el-form-item>
                  <el-form-item
                    label="过期间隔(秒)"
                    prop="expire"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.desk_option.expire"
                    ></el-input>
                  </el-form-item>
                  <el-form-item
                    label="状态同步间隔(秒)"
                    prop="async_time"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.desk_option.async_time"
                    ></el-input>
                  </el-form-item>
                  <el-form-item
                    label="最大局数"
                    prop="max_round"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.desk_option.max_round"
                    ></el-input>
                  </el-form-item>
                  <el-form-item
                    label="房间密码"
                    prop="pwd"
                    class="innerForm_item"
                  >
                    <el-input v-model="room.desk_option.pwd"></el-input>
                  </el-form-item>
                </el-form>
              </el-form-item>
              <el-form-item>
                <el-form
                  status-icon
                  ref="ruleForm"
                  label-width="100px"
                  class="demo-ruleForm innerForm"
                >
                  <el-form-item
                    label="生成桌子数量"
                    prop="default_desk"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.queue.default_desk"
                    ></el-input>
                  </el-form-item>
                  <el-form-item
                    label="玩家数"
                    prop="min"
                    class="innerForm_item"
                  >
                    <el-input v-model.number="room.queue.min" class="minInput">
                      <template slot="prepend">min</template>
                    </el-input>
                    -
                    <el-input v-model.number="room.queue.max" class="minInput">
                      <template slot="append">max</template>
                    </el-input>
                  </el-form-item>
                  <el-form-item
                    label="匹配方式"
                    prop="type"
                    class="innerForm_item"
                  >
                    <el-radio-group
                      v-model.number="room.queue.type"
                      size="medium"
                    >
                      <el-radio :label="1">桌号进入</el-radio>
                      <el-radio :label="2">人齐开桌</el-radio>
                      <el-radio :label="3">查找空桌</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item
                    label="查找桌子规则"
                    prop="enter_desk_type"
                    class="innerForm_item"
                  >
                    <el-radio-group
                      v-model.number="room.queue.enter_desk_type"
                      size="medium"
                    >
                      <el-radio :label="0">无</el-radio>
                      <el-radio :label="2">携带金币和桌内货币关系</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item
                    label="同桌限制"
                    class="innerForm_item"
                    prop="exclude_type"
                  >
                    <el-checkbox-group v-model="room.queue.exclude_type">
                      <el-checkbox :label="1" name="exclude_type"
                        >IP限制</el-checkbox
                      >
                      <el-checkbox :label="2" name="exclude_type"
                        >设备码限制</el-checkbox
                      >
                      <el-checkbox :label="3" name="exclude_type"
                        >真实姓名限制</el-checkbox
                      >
                      <el-checkbox :label="4" name="exclude_type"
                        >同桌匹配次数</el-checkbox
                      >
                    </el-checkbox-group>
                  </el-form-item>
                  <el-form-item
                    label="同桌游戏次数上限"
                    prop="max_requeue"
                    v-show="room.queue.exclude_type == 4"
                    class="innerForm_item"
                  >
                    <el-input
                      v-model.number="room.queue.max_requeue"
                    ></el-input>
                  </el-form-item>
                </el-form>
              </el-form-item>

              <el-form-item class="innerForm_item">
                <el-form
                  status-icon
                  ref="ruleForm"
                  label-width="100px"
                  class="demo-ruleForm innerForm"
                >
                  <el-form-item
                    label="启用机器人"
                    prop="open_robot"
                    class="innerForm_item"
                  >
                    <el-radio-group
                      v-model.number="room.robot.open"
                      size="medium"
                    >
                      <el-radio :label="false">不启用</el-radio>
                      <el-radio :label="true">启用</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item
                    label="等待时间"
                    v-show="room.robot.open == true"
                    class="innerForm_item"
                    ><el-input
                      v-model.number="room.robot.add_time_min"
                      class="minInput"
                    >
                      <template slot="prepend">min</template></el-input
                    >
                    -
                    <el-input
                      v-model.number="room.robot.add_time_max"
                      class="minInput"
                    >
                      <template slot="append">max</template></el-input
                    >
                    <!-- <label>max</label> -->
                  </el-form-item>
                </el-form>
              </el-form-item>
              <el-form-item class="innerForm_item">
                <el-button type="primary" size="medium" @click="submitRoom"
                  >提交</el-button
                >
                <el-button
                  type="danger"
                  size="medium"
                  @click="resetForm('room')"
                  >重置</el-button
                >
              </el-form-item>
            </el-form>
          </el-dialog>
          <el-dialog
            title="修改状态"
            :visible.sync="change_staus.dialogFormVisible"
          >
            <el-form :model="change_staus">
              <el-form-item label="修改游戏状态" label-width="100px">
                <el-radio-group v-model.number="change_staus.status">
                  <el-radio :label="0">隐藏</el-radio>
                  <el-radio :label="1">正常</el-radio>
                  <el-radio :label="2">维护</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="change_staus.dialogFormVisible = false"
                >取 消</el-button
              >
              <el-button type="primary" @click="commitStatus">确 定</el-button>
            </div>
          </el-dialog>
        </template>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        app_id: "",
        channel_code: ""
      },
      game: {
        game_id: null,
        type: null,
        name: null,
        // status: 0,
        icon: null,
        game_key: null,
        lobby_server: {
          key: null,
          id: null
        },
        currency_type: null,
        admin_status: null,
        publish_status: null
      },
      dialogFormVisible: false,
      change_staus: {
        game_id: null,
        status: null,
        obj: null, //status admin_status
        dialogFormVisible: false,
        index: null
      },
      room: {
        game_id: null,
        room_id: null,
        roomName: null,
        roomIcon: 0,
        status: 0,
        desk_type: null,
        sort: 1,
        roles: [],
        tags: [],
        type: null,
        lock_desk: false,
        lobby_server: {
          key: null,
          id: null
        },
        game_option: {
          currency_type: 1
        },
        desk_option: {
          enter_currency_type: 1,
          pump: 0,
          roomScore: 0,
          cost: 0,
          enter: [0, 0],
          expire: 0,
          async_time: 20,
          max_round: 0,
          pwd: null
        },
        queue: {
          default_desk: 0,
          min: 1,
          max: 0,
          type: 1,
          enter_desk_type: 3,
          exclude_type: [],
          max_requeue: 1
        },
        robot: {
          open: false,
          add_time_min: 0,
          add_time_max: 0
        }
      },
      arraystr: {
        roles: "",
        tags: ""
      },
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      action: {
        obj: "",
        type: "",
        title: ""
      },
      user: this.$store.getters['user/getUser'],
    };
  },
  methods: {
    getGameList() {
      var that = this;
      that.responseData = [];
      this.$api.game.gameList(this.requestParam).then(res => {
        if (res.data.success === true) {
          that.responseData = res.data.data.game;
          that.page.total = that.responseData.length;
          const start = (that.page.current_page - 1) * that.page.page_size;
          const end =
            that.page.total > that.page.current_page * that.page.page_size
              ? that.page.current_page * that.page.page_size
              : that.responseData.length;
          that.page.list = that.responseData.slice(start, end);
        } else {
          this.$message({
            showClose: true,
            message: "获取游戏列表失败",
            type: "error"
          });
        }
      });
      //that.responseData = json.data.game;
    },
    updateGame(game_id) {
      this.dialogFormVisible = true;
      this.action.type = "update";
      this.action.obj = "game";
      this.action.title = "游戏更新";

      let that = this;
      this.$api.game.getGameById({ game_id: game_id }).then(res => {
        if (res.data.success === true) {
          if (res.data.data.game_info != null) {
            that.game = res.data.data.game_info;
          }
        } else {
          this.$message({
            showClose: true,
            message: "获取游戏数据失败,请重试!",
            type: "error"
          });
          this.dialogFormVisible = false;
        }
      });
    },
    addGame() {
      this.resetGame();
      this.dialogFormVisible = true;
      this.action.type = "add";
      this.action.obj = "game";
      this.action.title = "游戏新增";
    },
    checkGameId() {
      this.$api.game.getGameById({ game_id: this.game.game_id }).then(res => {
        if (res.data.success === true) {
          if (res.data.data.game_info != null) {
            this.$message({
              showClose: true,
              message: "该游戏ID已存在!",
              type: "error"
            });
            this.resetGame();
          }
        }
      });
    },
    addRoom(game_id) {
      this.room.game_id = game_id;
      this.action.obj = "room";
      this.action.type = "add";
      this.action.title = "房间新增";
      this.dialogFormVisible = true;
    },
    getGameById(game_id) {
      let that = this;
      this.$api.game.getGameById({ game_id: game_id }).then(res => {
        if (res.data.success === true) {
          if (res.data.data.game_info != null) {
            that.game = res.data.data.game_info;
          }
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    resetGame() {
      this.game = {
        game_id: null,
        type: null,
        name: null,
        status: 0,
        icon: null,
        game_key: null,
        lobby_server: {
          key: null,
          id: null
        },
        currency_type: null
      };
    },
    checkGame() {
      if (
        this.action.type == "update" &&
        (this.game.game_id == null || this.game.game_key == null)
      ) {
        return false;
      }
      this.game.lobby_server.key = this.game.lobby_server.key || "lobby";
      this.game.lobby_server.id = this.game.lobby_server.id || "lobby-server-1";
      this.game.currency_type = this.game.currency_type || 1;
      this.game.type = this.game.type || 0;
      this.game.name = this.game.name || "未命名";
      this.game.icon = this.game.icon || 0;
      return true;
    },
    submitGame() {
      if (!this.checkGame()) {
        this.$message({
          showClose: true,
          message: "参数有误!",
          type: "error"
        });
        return;
      }
      let that = this;
      if (this.action.obj == "game" && this.action.type == "add") {
        this.$api.game.addGame(this.game).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "新增游戏成功",
              type: "success"
            });
            that.getGameList();
            that.$refs.game.resetFields();
          } else {
            this.$message({
              showClose: true,
              message: "新增游戏失败",
              type: "error"
            });
          }
        });
      } else if (this.action.obj == "game" && this.action.type == "update") {
        this.$api.game.updateGame(this.game).then(res => {
          if (res.data.success === true) {
            this.$message({
              showClose: true,
              message: "更新游戏成功",
              type: "success"
            });
            that.getGameList();
            that.$refs.game.resetFields();
          } else {
            this.$message({
              showClose: true,
              message: "更新游戏失败",
              type: "error"
            });
          }
        });
      }
    },
    submitRoom() {
      let that = this;
      this.checkRoom();
      this.$api.game.addRoom(this.room).then(res => {
        if (res.data.success === true) {
          this.$message({
            showClose: true,
            message: "新增房间成功",
            type: "success"
          });
          that.arraystr.roles = "";
          that.arraystr.tags = "";
          that.$refs.room.resetFields();
        } else {
          this.$message({
            showClose: true,
            message: "新增房间失败",
            type: "error"
          });
        }
      });
    },
    checkRoom() {
      this.room.roles = this.arraystr.roles
        .replace(/\s/g, "")
        .replace(/，/g, ",")
        .split(",")
        .map(Number);
      this.room.tags = this.arraystr.tags
        .replace(/\s/g, "")
        .replace(/，/g, ",")
        .split(",")
        .map(Number);
      this.room.roomName = this.room.roomName || "未命名";
      this.room.roomIcon = this.room.roomIcon || 0;
      this.room.status = this.room.status || 0;
      this.room.desk_type = this.room.desk_type || 0;
      this.room.sort = this.room.sort || 1;
      if (this.arraystr.roles == "") {
        this.room.roles = [];
      }
      if (this.arraystr.tags == "") {
        this.room.tags = [];
      }
      this.room.roles = this.room.roles || [];
      this.room.type = this.room.type || 1;
      this.room.tags = this.room.tags || [];
      this.room.lock_desk = this.room.lock_desk || false;
      this.room.lobby_server = this.room.lobby_server || {};
      this.room.lobby_server.key = this.room.lobby_server.key || "lobby";
      this.room.lobby_server.id = this.room.lobby_server.id || "lobby-server-1";
      this.room.game_option = this.room.game_option || {};
      this.room.game_option.currency_type =
        this.room.game_option.currency_type || 1;
      this.room.desk_option = this.room.desk_option || {};
      this.room.desk_option.enter_currency_type =
        this.room.desk_option.enter_currency_type || 1;
      this.room.desk_option.enter[0] = this.room.desk_option.enter[0] || 0;
      this.room.desk_option.enter[1] = this.room.desk_option.enter[1] || 0;
      this.room.desk_option.roomScore = this.room.desk_option.roomScore || 0;
      this.room.desk_option.cost = this.room.desk_option.cost || 0;
      this.room.desk_option.expire = this.room.desk_option.expire || 0;
      this.room.desk_option.async_time = this.room.desk_option.async_time || 20;
      this.room.desk_option.max_round = this.room.desk_option.max_round || 0;
      this.room.desk_option.pwd = this.room.desk_option.pwd || "";
      this.room.queue.default_desk = this.room.queue.default_desk || 0;
      this.room.queue.min = this.room.queue.min || 1;
      this.room.queue.max = this.room.queue.max || 0;
      this.room.queue.type = this.room.queue.type || 3;
      this.room.queue.enter_desk_type = this.room.queue.enter_desk_type || 3;
      this.room.queue.exclude_type = this.room.queue.exclude_type || [];
      this.room.queue.max_requeue = this.room.queue.max_requeue || 1;
      this.room.robot = this.room.robot || {};
      this.room.robot.open = this.room.robot.open || false;
      this.room.robot.add_time_min = this.room.robot.add_time_min || 0;
      this.room.robot.add_time_max = this.room.robot.add_time_max || 0;
    },
    openChangeStatus(game_id, status, obj, index) {
      this.change_staus = {
        game_id: game_id,
        status: status,
        dialogFormVisible: true,
        obj: obj
      };
    },
    commitStatus() {
      let that = this;
      if (this.change_staus.obj == "admin_status") {
        this.$api.game
          .updateGameAdminStatus({
            game_id: this.change_staus.game_id,
            admin_status: this.change_staus.status
          })
          .then(res => {
            if (res.data.success === true) {
              this.$message({
                showClose: true,
                message: "修改状态成功!",
                type: "success"
              });
              that.getGameList();
            } else {
              this.$message({
                showClose: true,
                message: "修改失败",
                type: "error"
              });
            }
          });
      } else if (this.change_staus.obj == "status") {
        this.$api.game
          .updateGameStatus({
            game_id: this.change_staus.game_id,
            status: this.change_staus.status
          })
          .then(res => {
            if (res.data.success === true) {
              this.$message({
                showClose: true,
                message: "修改状态成功!",
                type: "success"
              });
              that.getGameList();
            } else {
              this.$message({
                showClose: true,
                message: "修改失败",
                type: "error"
              });
            }
          });
      }
      this.change_staus = {
        game_id: null,
        status: null,
        obj: null,
        dialogFormVisible: false
      };
    },
    pushGame(game_id) {
      let that = this;
      this.$api.game.pushGame({ game_id: game_id }).then(res => {
        if (res.data.success === true) {
          this.$message({
            showClose: true,
            message: "发布游戏成功",
            type: "success"
          });
          that.getGameList();
        } else {
          this.$message({
            showClose: true,
            message: "发布游戏失败",
            type: "error"
          });
        }
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.page.list = this.responseData.slice(
        (this.page.current_page - 1) * this.page.page_size,
        this.responseData.length > this.page.current_page * this.page.page_size
          ? this.page.current_page * this.page.page_size
          : this.responseData.length
      );
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    }
  },
  // mounted: function() {
  //   this.user = user;
  // }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
</style>



